:root {
  --primary: #3b5b81;
  --secondary: #abd3eb;
  --tertiary: #122769;
  --light: #fafafa;
  --dark: #41899b;
  --error: rgb(163, 71, 76);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 80%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: black;
  background-image: url(https://geweb.azureedge.net/-/media/Images/Giant-Eagle/shop/Produce/produce-hero-image.ashx);
  background-repeat: no-repeat;
  background-size: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', Monospace;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;

}

h2 {
  font-size: 2.5rem;
  font-family:Arial, Helvetica, sans-serif ;
  font-weight: 200;
  color: black;


}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: black;
  text-decoration: none;
  font-weight: 900;
}

a:hover {
  text-decoration: underline;
}

header {
  opacity: 75%;
  background-blend-mode: color-burn);
  height:100px;
  justify-content: space-between;
    flex-wrap: wrap;
    background-size:cover;
    min-width: 100%;
    padding-bottom: 2%;
    display: flex;
    float: right;


}

footer{
  background: black;
  opacity: 90%;
  height: 43px;
  color: var(--light);
  text-align: center;
  position: fixed;
   left: 0;
   bottom: 0;
   width: 100%;
   font-size: 15px;




}

header a {
  color: var(black);
  font-weight: 900;
  font-size: larger;
  margin: 1.5em;
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 8px;
    border: none;
  outline: none;
  cursor: pointer;
  margin-right: 4px;
  margin-left: 4px;
  margin-top: 10px;
  background-color: var(--secondary);
  background-color: white; 
  color: black; 
  border: 2px solid black;
  box-shadow: 5px 5px 8px black;
}

button:hover {
  background-color: #008CBA;
  color: white;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
  opacity: 80%;
  border-radius: 0px 16px 16px 16px;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
  color: black;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
  
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
  border-radius: 16px;
  border: 4px solid black;
  margin: 2em;
  box-shadow: 10px 10px 20px black;
}

.error-text {
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}